@charset "utf-8"; //
// SASS用関数の設定
//
// ==========================================================================
// ミックスイン
// ==========================================================================
@mixin noto_sans($weight:normal) {
	font-family: 'Noto Sans Japanese', "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
	font-weight: $weight;
}
@mixin roboto($weight:normal) {
	font-family: "Roboto", "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
	font-weight: $weight;
}
@mixin open_sans($weight:normal) {
	font-family: "Open Sans", "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
	font-weight: $weight;
}
@mixin yumincho {
	font-family: "游明朝体", "Yu Mincho", YuMincho, "Hiragino Kaku Gothic ProN", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, "ＭＳ ゴシック", sans-serif;
	// font-weight: bold;
}
@mixin merriweather {
	font-family: 'Merriweather', serif;
}
@mixin noto_serif {
	font-family: 'Noto Serif Japanese', serif;
}
@mixin roboto_slab {
	font-family: 'Roboto Slab', serif;
}
@mixin reimin {
	font-family: "a-otf-ud-reimin-pr6n";
}
@mixin meiryo {
	font-family: 'メイリオ', 'Meiryo';
}
@mixin yumincho {
	font-family: yu-mincho-pr6, sans-serif;
	font-style : normal;
	font-weight: 100;
}
// ==========================================================================
// nth-of-type
// ==========================================================================
@mixin first($num) {
	 @if $num == 1{
		&:first-of-type {
			@content;
		}
	}
	@else {
		&:nth-of-type(-n + #{$num}) {
			@content;
		}
	}
}
@mixin last($num) {
	&:nth-last-of-type(-n + #{$num}) {
		@content;
	}
}
@mixin even() {
	&:nth-of-type(even) {
		@content;
	}
}
@mixin odd() {
	&:nth-of-type(odd) {
		@content;
	}
}
@mixin nth($num) {
	 @if $num == first{
		&:first-of-type {
			@content;
		}
	}
	@elseif $num == last {
		&:last-of-type {
			@content;
		}
	}
	@else {
		&:nth-of-type(#{$num}) {
			@content;
		}
	}
}
@mixin between($first, $last) {
	&:nth-of-type(n + #{$first}):nth-of-type(-n + #{$last}) {
		@content;
	}
}
// ==========================================================================
// not
// ==========================================================================
@mixin not($num) {
	 @if $num == 1{
		&:not(:first-of-type) {
			@content;
		}
	}
	@elseif $num == last {
		&:not(:last-of-type) {
			@content;
		}
	}
	@else {
		&:not(:nth-of-type(#{$num})) {
			@content;
		}
	}
}

// ==========================================================================
// position - absolute
// ==========================================================================
@mixin absPosition ($top: auto, $right: auto, $bottom: auto, $left: auto) {
	position: absolute;
	top     : $top;
	right   : $right;
	bottom  : $bottom;
	left    : $left;
}
// ==========================================================================
// Placeholder Color
// ==========================================================================
@mixin placeholderColor($color) {
	&:placeholder-shown {
		color: $color;
	}

	&::-webkit-input-placeholder {
		color: $color;
	}

	&:-moz-placeholder {
		color  : $color;
		opacity: 1;
	}

	&::-moz-placeholder {
		color  : $color;
		opacity: 1;
	}

	&:-ms-input-placeholder {
		color: $color;
	}
}
// ==========================================================================
// opacity
// ==========================================================================
@mixin opacity {
	transition: .3s;

	&:hover {
		opacity: .7;
	}
}
// ==========================================================================
// borderbottom
// ==========================================================================
@mixin borderbottom_heading {
	position: relative;

	&:before {
		position  : absolute;
		content   : "";
		width     : 100%;
		height    : 1px;
		background: #f4f4f4;
		bottom    : -32px;
	}

	&:after {
		position  : absolute;
		content   : "";
		width     : 20%;
		height    : 1px;
		background: $primary;
		bottom    : -32px;
		left      : 0;
	}
}

@mixin borderbottom_heading_sp {
	position: relative;

	&:before {
		position  : absolute;
		content   : "";
		width     : 100%;
		height    : 1px;
		background: #f4f4f4;
		bottom    : -16px;
	}

	&:after {
		position  : absolute;
		content   : "";
		width     : 20%;
		height    : 1px;
		background: $primary;
		bottom    : -16px;
		left      : 0;
	}
}
// ==========================================================================
// 変数
// ==========================================================================
// プライマリーカラー
$primary: #6b8260; // セカンダリーカラー
$secondary: #XXXXXX; // アクセントーカラー
$accent: #xxx; // ベースワイズ
$width: 1170px;
$width_sp: 90%;
$frame_img_color: #385723;
