@charset "utf-8";

//
// ベース用CSS
//

/* Reset
---------------------------------------------------------------------------*/

* {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	-moz-text-size-adjust: none;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	text-size-adjust: 100%;
}

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
    margin:0;
    padding:0;
    border:0;
    outline:0;
    font-size:100%;
	font-weight: normal;
    vertical-align:baseline;
    background:transparent;
}

body {
    line-height:1;
}

article,aside,details,figcaption,figure,
footer,header,hgroup,menu,nav,section,main {
    display:block;
}

ul,ol {
    list-style:none;
}

blockquote, q {
    quotes:none;
}

blockquote:before, blockquote:after,
q:before, q:after {
    content:'';
    content:none;
}

a {
    margin:0;
    padding:0;
    font-size:100%;
    vertical-align:baseline;
    background:transparent;
}

// change colours to suit your needs
ins {
    background-color:#ff9;
    color:#000;
    text-decoration:none;
}


mark {
    background-color:#ff9;
    color:#000;
    font-style:italic;
    font-weight:bold;
}

del {
    text-decoration: line-through;
}

abbr[title], dfn[title] {
    border-bottom:1px dotted;
    cursor:help;
}

table {
    border-collapse:collapse;
    border-spacing:0;
}


hr {
    display:block;
    height:1px;
    border:0;
    border-top:1px solid #cccccc;
    margin:1em 0;
    padding:0;
}

// Form Reset
input, select {
    vertical-align: middle;
}

input {
	-webkit-appearance: none; 
}

button, fieldset, form, input, label,
legend, select, textarea {
	font-family: inherit;
	font-size: 100%;
	font-style: inherit;
	font-weight: inherit;
	margin: 0;
	padding: 0;
	vertical-align: baseline;
	border: none;
	outline: none;
}

a:focus, *:focus {
	outline: none;
}

input[type="button"],input[type="submit"] {
	-webkit-appearance: none;
}

/* General
---------------------------------------------------------------------------*/

// ==========================================================================
// Clearfix
// ==========================================================================

.clearfix:before,
.clearfix:after {
  	content: " ";
    display: table;
}

.clearfix:after {
	clear: both;
}

.clearfix {
	*zoom: 1;
}

/* Hides from IE-mac \*/
* html .clearfix {
	height: 1%;
}
.clearfix {
	display: block;
}
/* End hide from IE-mac */

// ==========================================================================
// Box Layouts
// ==========================================================================

.none {
	display: none;
}

.left {
	float: left;
}

.right {
	float: right;
}

.inline_block {
	display: inline-block;
}

.inline {
	display: inline;
}

.block {
	display: block;
}

.relative {
	position: relative;
}

.img_center {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

// ==========================================================================
// Font Style
// ==========================================================================

// 装飾

.align_right {
	text-align: right;
}

.align_left {
	text-align: left;
}

.align_center {
	text-align: center;
}

.indent {
	text-indent: 1rem;
}

// 装飾

.bold {
	font-weight: bold;
}

.overline {
	text-decoration: overline;
}

.underline {
	text-decoration: underline;
}

.blink {
	text-decoration: blink;
}

.line_through {
	text-decoration: line-through;
}

.italic {
	font-style: italic;
}

// 文字色

.red {
	color: red ;
}

.blue {
	color: blue ;
}

.green {
	color: green ;
}

.gold {
	color: gold ;
}

.grey {
	color: grey ;
}

.black {
	color: black ;
}

.white {
	color: white ;
}
