.top_hero {
	height             : 925px;
	position           : relative;
	background-image   : url("../images/home/mian_02.png");
	background-position: bottom center;
	background-size    : contain;

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	h2 {
		position : absolute;
		top      : 50%;
		left     : 50%;
		transform: translateY(-50%);
		color    : #fff;
		font-size: 4.2rem;
		@include yumincho;
	}
}
